<template>
    <div class="home" ref="homeRef">
        <router-link v-for="product in products" :key="product.id" :to="`/detail?des=${product.des}&detailCode=${product.detailCode}`">
            <card
                    :avatar="product.avatar"
                    :author="product.author"
                    :time="product.time"
                    :des="product.des"
                    :imgs="product.imgs"
                    :visited="product.visited"
                    :loading="loading"
            ></card>
        </router-link>
    </div>
</template>

<script>
    import Card from '../components/Card'

    export default {
        name: 'home',
        components: {
            'card': Card
        },
        data() {
            return {
                products: [{},{},{}],
                loading: true
            }
        },
        mounted() {
            this.init();
        },
        methods: {
          init() {
            this.$ajax.get('https://api.github.com/gists/0d249a1145fce85744b945898a910331').then((res)=>{
                this.products = JSON.parse(res.data.files.products.content);
                this.loading = false;
            })
          }
        },
        //记住滚动位置
        beforeRouteLeave(to, from, next) {
            this.$store.commit('setMainScrollTop', this.$refs.homeRef.scrollTop)
            next();
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                vm.$refs.homeRef.scrollTop = vm.$store.state.homeScrollTop
            })
        }
    }
</script>

<style lang="scss" scoped>
    .home {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>
