<template>
    <div class="card" :class="{'card-loading': loading}">
        <div class="card-header">
            <a class="card-header-left">
                <img v-if="!loading" class="card-avatar" :src="avatar"/>
            </a>
            <div class="card-header-right">
                <p class="card-author">{{author}}</p>
                <p class="card-time">{{time}}</p>
            </div>
        </div>
        <div class="card-section">
            <p class="card-des">{{des}}</p>
            <div class="card-imgs">
                <div class="card-imgs-box-1" v-for="img in imgs">
                    <div class="card-imgs-box-2">
                        <img class="card-img" :src="img" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="card-footer">
            <span v-if="!loading">{{visited}}</span>
            <svg v-if="!loading" class="card-icon">
                <use xlink:href="#icon-looked"></use>
            </svg>
        </div>
    </div>
</template>

<script>
    export default {
        name: "card",
        props: {
            avatar: {
                type: String,
                default: ''
            },
            author: {
                type: String,
                default: ''
            },
            time: {
                type: String,
                default: ''
            },
            des: {
                type: String,
                default: ''
            },
            imgs: {
                type: Array,
                default: () => ['','','']
            },
            visited: {
                type: Number,
                default: 0
            },
            loading: {
                type: Boolean,
                default: false
            }
        }
    }
</script>

<style  scoped lang="scss" >
    .card {
        margin-top: 15px;

        border-bottom: 1px solid #ddd;
        .card-header {
            display: flex;
            align-items: center;

            padding: 0 15px;

            min-height: 52px;

            .card-header-left {
                width: 50px;
                height: 50px;

                border-radius: 50%;
                border: 1px solid #eee;

                font-size: 0;

                overflow: hidden;

                .card-avatar {
                    width: 100%;
                    height: 100%;
                }
            }
    .card-header-right {
        display: flex;
        flex: 1;
        justify-content: space-between;
        flex-direction: column;

        margin-left: 5px;

    .card-author {
        margin-bottom: 10px;

        min-height: 25px;

        font-size: 22px;

        color: #333;
    }

    .card-time {
        min-height: 16px;

        font-size: 14px;

        color: #999;
    }
    }
        }
    .card-section {
        padding: 0 15px;

        color: #444;
    .card-des {
        margin-top: 10px;

        min-height: 18px;
    }
        .card-imgs {
            display: flex;

            margin-top: 10px;
            .card-imgs-box-1 {
                width: 33.33%;
                padding-right: 4px;
    &:last-child {
                    padding: 0;
                 }
                .card-imgs-box-2 {
                    position: relative;
                    height: 0;
                    padding-bottom: 100%;
                    .card-img {
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
    }
    .card-footer {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        margin: 6px 15px 9px 15px;

        min-height: 24px;

        font-size: 16px;

        color: #444;
        .card-icon {
            margin-left: 4px;

            width: 24px;
            height: 24px;

            fill: currentColor;
        }
    }
    &.card-loading {

        .card-author {
            animation: loading 1s ease-in-out infinite;
        }
    .card-time {
        animation: loading 1s ease-in-out -.5s infinite;
    }
        .card-footer {
            transform-origin:top right;
            transform: scaleX(0.2);
        }
    .card-author,
    .card-time,
    .card-header-left,
    .card-imgs-box-2,
        .card-des,
        .card-footer {
            background-color: #eee;
        }
        .card-author,
        .card-time,
        .card-des {
            transform-origin: top left;
        }
        @keyframes loading {
            0% {
                transform: scaleX(0.4);
            }

            50% {
                transform: scaleX(0.8);
            }
            100% {
                transform: scaleX(0.4);
            }
        }
     }
    }
</style>