import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      homeScrollTop: 0,
      detailScrollTop: 0
  },
  mutations: {
    setMainScrollTop(state,scrollTop) {
      state.homeScrollTop = scrollTop
    },
      setDetailScrollTop(state,scrollTop) {
          state.detailScrollTop = scrollTop
      }
  },
  actions: {

  }
})
